import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { QuestionService } from '../services/question.service';

@Component({
  selector: 'app-addquestion',
  templateUrl: './addquestion.page.html',
  styleUrls: ['./addquestion.page.scss'],
})
export class AddquestionPage implements OnInit {

  submitForm:FormGroup;
  current:any=1;
  questions:any;

  constructor(private fb:FormBuilder,private router:Router,private quesservice:QuestionService) {
    this.submitForm=fb.group({
      question: ['', Validators.required],
      language: ['', Validators.required],
      answer: ['', Validators.required],
      answer2: ['', Validators.required]
    });
   }

  ngOnInit() {
  }

  submitQuestion(){
    console.log("done!!");
    this.quesservice.addQuestion(this.submitForm.value).subscribe(data=>{
      if(data['status']==200){
        console.log(data);
        this.router.navigate(["/admin/questionsadmin"]);
      }
      else{
        console.log(data);
      }
    })
    this.submitForm.reset();
    this.current=1;

  }

  moreAns(){
    this.current=0;
  }

  getAllQustions(){
    this.quesservice.getQuestions().subscribe(data=>{
      this.questions=data;
      console.log(this.questions);
    })
  }

}
